import path from "path";

export default class {

    constructor() {
        this.loadPaths();
    }

    loadPaths() {

        if (global.koahub == undefined) {
            global.koahub = {};
        }

        let mainFile = process.argv[1];
        let mainPath = path.dirname(mainFile);
        let projectPath = process.cwd();
        let appName = path.relative(projectPath, path.dirname(mainFile));
        let appPath = path.dirname(mainFile);

        koahub.paths = {
            mainFile: mainFile,
            mainPath: mainPath,
            appName: appName,
            appPath: appPath
        };

        console.log(koahub.paths);
    }

    loadPath(path) {
        return path.resolve(koahub.paths.serverName, path);
    }
}